@import url(https://fonts.googleapis.com/css?family=Asap+Condensed:700|Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Asap+Condensed:700|Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Asap+Condensed:700|Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Asap+Condensed:700|Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button {
    font-family: 'Asap Condensed', sans-serif;
    font-size: 18px;
    background-color: #272727;
    color: white;
    border-radius: 5px;
    border-style: none;
    
    cursor: pointer;
    margin: 8px;
}

.button:hover {
    font-family: 'Asap Condensed', sans-serif;
    font-size: 18px;
    background-color: #ff8c1a;
    color: white;
    border-radius: 5px;
    border-style: none;   
    cursor: pointer;
}
.kitchen-order {
    display: flex;
    justify-content: space-around;
}

.order-card {
    float: left;
    justify-content: center;
    width: 200px;
    border-radius: 5px;
    box-shadow: -5px 7px 13px rgb(116, 113, 113);
    cursor: pointer;
    padding: 5px;
    margin: 8px;
}

.order-name, .order-table, .order-order, .order-item, .order-date {
    font-family: 'Asap Condensed', sans-serif;
    font-size: 20px;
    color: #65382e;
    text-align: center;
    margin: 3px;
    margin-top: 18px;
}
*{-webkit-transition:all 0.3s ease-in-out;transition:all 0.3s ease-in-out;}

.nav {
  clear:both;
  overflow:auto;
  background-color: #424242;
  padding: 10px;
}

nav {
  float:right;
}

.logo img {
  float:left;
}

ul li {
  display: inline-block; 
  padding:15px;
}

.link {
  color: white;
  text-decoration: none;
  font-family: 'Asap Condensed', sans-serif;
  font-size: 35px;
}

.link:hover {
  color:orange;
}
.menu-card {
    float: left;
    justify-content: center;
    height: 230px;
    width: 200px;
    border-radius: 5px;
    box-shadow: -5px 7px 13px rgb(116, 113, 113);
    cursor: pointer;
    padding: 5px;
    margin: 8px;
}

.div-image {
    display: flex;
    justify-content: center;
}

.image {
    width: 130px;
    display: flex;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
}

.name {
    font-family: 'Asap Condensed', sans-serif;
    font-size: 20px;
    color: #65382e;
    text-align: center;
    margin: 3px;
    margin-top: 18px;
} 

.price {
    font-family: 'Asap Condensed', sans-serif;
    font-size: 18px;
    color: #65382e;
    text-align: center;
    margin: 3px;
}
.menu {
    display: flex;
    justify-content: space-around;
}

.menu-item {
    display: flex;
    flex-flow: wrap;
    flex-direction: row;
    justify-content: center;
}

.menu-breakfast, .menu-lunch {
    border-top: 5px;
    flex-direction: row;
    display: flex;
    justify-content: center;
    flex-flow: wrap;
}

.order {
    display:flex;
    border: 5px;
    font-family: 'Asap Condensed', sans-serif;
    font-size: 25px;
    color: #65382e;
    text-align: center;
    justify-content: center;
    margin-bottom: 100px;
}

.lunch, .breakfast, .client, .table, .order-title {
    font-family: 'Asap Condensed', sans-serif;
    font-size: 25px;
    color: #65382e;
    text-align: center;
}

.modal {
    font-family: 'Asap Condensed', sans-serif;
    font-size: 25px;
    color: #65382e;
    text-align: center;
}
.input {
    border-radius: 3px;
    width: 300px;
    height: 30px;
    font-family: 'Asap Condensed', sans-serif;
    font-size: 30px;
    color: #65382e;
    text-align: center;
    border-style: solid;
}
.delivery-order {
    display: flex;
    justify-content: space-around;
}

.order-card {
    float: left;
    justify-content: center;
    width: 200px;
    border-radius: 5px;
    box-shadow: -5px 7px 13px rgb(116, 113, 113);
    cursor: pointer;
    padding: 5px;
    margin: 8px;
}

.order-name, .order-table, .order-order, .order-item, .order-date {
    font-family: 'Asap Condensed', sans-serif;
    font-size: 20px;
    color: #65382e;
    text-align: center;
    margin: 3px;
    margin-top: 18px;
}
ul li {
    display: inline-block;
    padding:15px;
}
  
.link-home {
    color: white;
    text-decoration: none;
    font-family: 'Asap Condensed', sans-serif;
    font-size: 35px;
}

.links {
    display: flex; 
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 100vh;
}

.home {
    background-image: url('https://image.freepik.com/foto-gratis/hamburguesa-casera-hamburguesa-queso-carne-sobre-fondo-madera-hamburguesa-casera-clasica_90380-271.jpg');
    background-size: cover;
    height: 100vh;
}

  
.link-home:hover {
    color:orange;
}
